.pagination-container {
  background-color: rgba(111, 111, 6, 0.109);
  display: flex;
  justify-content: center;
  margin-bottom: 100px;
}
.pagination-div {
  margin: 10px 3px;
  padding: 5px 7px;
  border-radius: 3px;
  border: 1px solid rgba(111, 111, 6, 0.687);
  cursor: pointer;
}
.pagination-div:hover {
  box-shadow: 0px 0px 5px rgba(111, 111, 6, 0.403);
}
.active {
  color: white;
  background-color: rgba(111, 111, 6, 0.687);
}
