input[type="search"] {
  border: none;
  height: 25px;
  border-bottom: 1px solid gray;
  padding: 0px 15px;
  width: 85%;
  max-width: 450px;
  outline: none;
  font-size: 17px;
}

@media screen and (max-width: 700px) {
  input[type="search"] {
    padding: 0px 10px;
    font-size: 15px;
  }
}
